html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Staatliches", cursive;
  font-size: 16px;
  line-height: 1.5;
}

p {
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  width: 100%;
}

h1, h2, h3 {
  text-align: center;
  font-family: "Staatliches", cursive;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

button,
input[type=button] {
  cursor: pointer;
}

ul {
  padding: 0;
}
ul li {
  list-style-type: none;
}

.margin-zero {
  margin: 0;
}

header {
  display: flex;
  justify-content: center;
  padding: 2rem 0;
}

.navbar {
  background-color: #a15b3d;
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
  width: 100%;
}

.navbar__logo {
  max-width: 10rem;
  margin-top: 0.7rem;
}

.navbar__link {
  font-family: "Staatliches", cursive;
  color: #fff;
  font-size: 3rem;
}

.navbar__link:hover,
.navbar__link--active {
  color: #b11b23;
}

.navbar__form {
  display: flex;
  justify-content: center;
  padding: 1rem 0;
  gap: 2rem;
}

.navbar__form__text,
.navbar__form__btn,
.newsblock__content__btn,
.linksblock__social__btn {
  border: none;
  border-radius: 0.5rem;
  background-color: #fff;
}

.navbar__form__btn {
  width: 2.8rem;
  border-radius: 50%;
}

.newsblock {
  background-color: #d1d1d1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 16rem;
  text-align: center;
  margin-top: 2rem;
}

.newsblock__content {
  width: 100%;
  height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.newsblock__content__btn {
  width: 15rem;
  height: 5rem;
  color: #a15b3d;
  border: 0.1rem solid #a15b3d;
}

.linksblock {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  gap: 2rem;
}

.linksblock__links {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding-left: 16rem;
  align-self: center;
  justify-items: center;
}

.linksblock__icons {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 7rem 16rem;
  gap: 2rem;
  align-items: center;
  padding: 2rem 16rem 3rem 0;
}

.linksblock__icons__social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.linksblock__icons__social__btn {
  background-color: #d1d1d1;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  margin: 0 1rem 0 1rem;
}

.linksblock__icons__cert {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
  align-items: center;
  justify-items: center;
}

.linksblock__icons__cert__logo {
  background-color: #d1d1d1;
  width: 10rem;
  height: 5rem;
}

.item7 {
  grid-column: 1/3;
}

.item8 {
  grid-column: 3/5;
}

.item9 {
  grid-column: 5/7;
}

.footnoteblock {
  border-top: 0.1rem solid 900;
  padding: 0 16rem 0 16rem;
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.container {
  max-width: 120rem;
  height: auto;
  margin: 0 auto;
}

.bg {
  background-image: url(../images/min/stills/background-mountain.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 45rem;
}

.carousel {
  background-color: rgba(0, 0, 0, 0.65);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.fastnews {
  margin: 1rem 0;
  background-color: #a15b3d;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fastnews p {
  font: "Staatliches", cursive;
}

.discounts {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.discount {
  width: 22rem;
  height: auto;
  background-color: rgba(230, 99, 99, 0.322);
  border: 0.1rem solid red;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.banners {
  margin: 1rem 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  gap: 0.5rem;
}

.banner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  border: 0.1rem solid blue;
  border-radius: 0.5rem;
}

.banner__icon {
  grid-row: 1/3;
  text-align: center;
  margin: auto 0;
}

.banner__texto {
  grid-column: 2/3;
}

.catCards {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
}

.catCard {
  border: 0.1rem solid 900;
  display: grid;
  grid-template-columns: repeat(2, 12rem);
  grid-template-rows: repeat(6, 6rem);
  gap: 0.5rem;
}

.catCard__image {
  grid-column: 1/3;
  grid-row: 1/4;
}

.catCard__title {
  grid-column: 1/3;
  grid-row: 4/5;
}

.catCard__sections {
  grid-column: 1/2;
  grid-row: 5/7;
}

.catCard__expand {
  grid-column: 2/3;
  grid-row: 5/6;
}

.prodsReel__carousel {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5rem;
}

.product {
  background-color: #a15b3d;
  padding: 1rem;
}

.product__name {
  font-size: 4rem;
}

.product__price {
  font-size: 2.8rem;
  color: #b11b23;
}

.product__name, .product__price {
  font-family: "Staatliches", cursive;
  margin: 1rem 0;
  text-align: center;
  line-height: 1.2;
}